<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}" >
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
        :{{ cartItems.length }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in products" :key="item.productId" :item="item" />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            &nbsp;   &nbsp; 
          </p>
          <p class="cart__price">
            : <span>{{ totalPrice | numberFormat }} </span>
          </p>

          <router-link v-if="cartNotEmpty" tag="button" :to="{name: 'order'}"
                       class="cart__button button button--primery" type="submit">
             
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import CartItem from '@/components/CartItem.vue';

export default {
  name: 'CartPage',
  components: { CartItem },
  filters: { numberFormat },
  computed: {
    ...mapGetters({
      cartItems: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
    }),
    products() {
      return this.$store.getters.cartDetailProducts;
    },
    cartNotEmpty() {
      return this.$store.state.cartProducts.length > 0;
    },
  },
};
</script>

